:root {
    --bcgColor: transparent linear-gradient(119deg, #111111 0%, #292929 100%) 0% 0% no-repeat padding-box;
    --accentColor: #FFCD48;
    --lightColor: rgb(255, 255, 255);
    --darkColor: #1f1f1f;
    --burgerMenu: #000;
    --mainFont: "Montserrat", sans-serif;
    --secondFont: 'Alata', sans-serif;
}

/* KEYFRAMES */
@keyframes arrowAnimationRight {
    0% {
        opacity: 1;
        transform: translateX(0px) scale(1);
    }

    25% {
        opacity: 0;
        transform: translateX(10px) scale(0.9);
    }

    26% {
        opacity: 0;
        transform: translateX(-10px) scale(0.9);
    }

    55% {
        opacity: 1;
        transform: translateX(0px) scale(1);
    }
}

@keyframes arrowAnimationLeft {
    0% {
        opacity: 1;
        transform: translateX(0px) scale(1);
    }

    25% {
        opacity: 0;
        transform: translateX(-10px) scale(0.9);
    }

    26% {
        opacity: 0;
        transform: translateX(10px) scale(0.9);
    }

    55% {
        opacity: 1;
        transform: translateX(0px) scale(1);
    }
}

