    /** BURGER MENU **/
    .burgerMenuWrapper {
        position: fixed;
        //right: 4em;
        //top: 1em;
        font-family: "Roboto", Arial, sans-serif;
        z-index: 4;
        //top: 32px;
        right: 0;

        .burgerMenu {
            color: var(--lightColor);
            padding-top: 32px;
            margin: 0;
            outline: none;
            /* position: absolute;
            left: 0px; */
            border: none;
            background: transparent;
            border-radius: 0px 0px 0px 30px;
            cursor: pointer;
            -webkit-appearence: none;
            -webkit-tap-highlight-color: transparent;

            svg {
                height: 74px;
                stroke: var(--lightColor);
                stroke-width: 2px;
                stroke-linecap: round;
                stroke-linejoin: round;
                fill: none;
                display: block;
                position: relative;

                path {
                    transition: stroke-dasharray var(--duration, 0.85s) var(--easing, ease) var(--delay, 0s),
                        stroke-dashoffset var(--duration, 0.85s) var(--easing, ease) var(--delay, 0s);
                    stroke-dasharray: var(--array-1, 26px) var(--array-2, 100px);
                    stroke-dashoffset: var(--offset, 126px);
                    transform: translateZ(0);

                    &:nth-child(2) {
                        --duration: 0.7s;
                        --easing: ease-in;
                        --offset: 100px;
                        --array-2: 74px;
                        transform: scalex(80%) translateX(12px) translateY(-2px);
                    }

                    &:nth-child(3) {
                        --offset: 133px;
                        --array-2: 107px;
                        transform: scalex(50%) translateX(47px) translateY(-4px);
                    }
                }
            }

            &.active {
                svg {
                    path {
                        --offset: 57px;

                        &:nth-child(1),
                        &:nth-child(3) {
                            --delay: 0.15s;
                            --easing: cubic-bezier(0.2, 0.4, 0.2, 1.1);
                        }

                        &:nth-child(2) {
                            --duration: 0.4s;
                            --offset: 1px;
                            --array-1: 1px;
                            transform: scalex(100%) translateX(0) translateY(-2px);
                        }

                        &:nth-child(3) {
                            --offset: 58px;
                            transform: scalex(100%) translateX(0px) translateY(-4px);
                        }
                    }
                }
            }
        }
    }

