                        transform: rotatey(.5turn);
                    }

                    .flipFront,
                    .flipBack {
                        position: absolute;
                        top: 0;
                        right: 0;
                        bottom: 0;
                        left: 0;
                        backface-visibility: hidden;
                        transform-style: preserve-3d;
                    }



