                            }

                            .icon {
                                transition: all 0.45s cubic-bezier(0.65, 0, .076, 1);
                                position: absolute;
                                top: 0;
                                bottom: 0;
                                margin: auto;
                            }

                            .buttonText {
                                transition: all 0.45s cubic-bezier(0.65, 0, .076, 1);
                                position: absolute;
                                top: 0;
                                left: 10px;
                                right: 20px;
                                bottom: 0;
                                padding: 0.75rem 0;
                                margin: 0 0 0 1.85rem;
                                color: #fff;
                                line-height: 1.6;
                                text-align: center;
                                text-transform: uppercase;
                            }

                            &:hover {
                                .submitCircle {
                                    width: 100%;

                                    .icon {
                                        &.arrow {
                                            background: #242424;
                                            transform: translate(1rem, 0);
                                        }
                                    }
                                }

                                .buttonText {
                                    color: #242424;
                                }
                            }
                        }
                    }

                    .labelAbove,
                    .labelAbove input,
                    .labelAbove textarea {
                        display: block;
                        color: var(--lightColor);
                        font-family: var(--mainFont);
                        font-weight: bold;
                    }

                    input[type="text"],
                    input[type="email"] {
                        background: transparent;
                        border-top: none;
                        border-right: none;
                        border-left: none;
                        border-bottom: .5px solid var(--accentColor);
                        width: 80%;
                        height: 30px;
                    }

                    #formMessage {
                        width: 90%;
                        height: 20vh;
                        color: #3d3d3d;
                        margin-top: 1em;
                    }

                    fieldset {
                        border: none;
                        color: var(--lightColor);
                        width: 200px;

                        legend {
                            padding-top: 50px;
                            font-size: .8vw;
                            font-weight: bold;
                        }

                        label {
                            font-size: .7vw;
                        }
                    }
                }
            }
        }

        .footerWrapper {
            background: rgba(17, 17, 17, 0.555);


            .footerInnerContent {
                display: grid;
                align-items: center;
                grid-template-areas: "imprint contact socials";
                max-width: 95%;
                margin: auto;
                min-height: 50px;

                .footerSocials {
                    grid-area: socials;


                    a {
                        color: var(--lightColor);
                        padding-right: 1.5em;
                        font-size: 1.2em;
                        float: right;
                    }
                }

                .policyImprint {
                    grid-area: imprint;
                    color: var(--lightColor);
                    font-weight: lighter;
                    font-size: .7em;
                    opacity: .6;
                    text-transform: uppercase;

                    p {
                        display: inline-flex;
                        flex-direction: column;
                        padding-right: 1em;
                    }

                }

                .contactItems {
                    grid-area: contact;
                    justify-self: center;

                    i {
                        color: var(--lightColor);

                        span {
                            font-family: var(--mainFont);
                            font-weight: 400;
                            margin-left: .5em;
                            color: var(--lightColor);
                        }
                    }

                    .fa-at {
                        margin-left: 3em;
                    }
                }
            }
        }
    }
}